.commentsWrapper {
  width: 1020px;
  margin: 0 auto;
  background: #fff;
  border-radius: 9px;
  padding: 30px 0px;
  margin-bottom: 30px;
  .commentsTopBox {
    border-bottom: 1px solid #f2f2f2;
    margin-bottom: 18px;
    .commentsCounter {
      margin-bottom: 50px;
    }
    button {
      background: none;
      border: none;
      font-size: 14px;
      cursor: pointer;
      margin-right: 10px;
      left: 0;
      padding: 0 0px 15px 0px;
      margin-right: 30px;
      &:hover {
        color: #84aa8b;
      }
    }
    .active {
      font-weight: 600;
      border-bottom: 3px solid #4a7bda;
    }
  }
  .commentInput {
    width: 100%;
    padding: 15px;
    border-radius: 9px;
    background: #f7f7f7;
    border: 1px solid #dbdbdb4d;
    margin-bottom: 30px;
    &::placeholder {
      font-size: 16px;
    }
  }
}
